import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import image from './background.jpg'

const BASE_URL = 'http://10.50.44.16:8080/projects/';

const FetchDetails = ({ onDelete }) => {
    const [details, setDetails] = useState([]);
    const [deletedItems, setDeletedItems] = useState(() => {
        const savedItems = localStorage.getItem('deletedItems');
        return savedItems ? JSON.parse(savedItems) : [];
    });


    useEffect(() => {
        const fetchData = async () => {
            const response = await axios.get(BASE_URL + 'api/fetch-overall/');
            setDetails(response.data);
        };

        fetchData();
    }, []);

    useEffect(() => {
        // Save deleted items to localStorage whenever it changes
        localStorage.setItem('deletedItems', JSON.stringify(deletedItems));
    }, [deletedItems]);

    const formatDate = (date) => {
        const year = date.slice(2, 4);
        const month = date.slice(5, 7);
        const day = date.slice(8, 10);  // Assuming the date is in DDMMRR format
        return `${day}${month}${year}`;
    };

    const tableDate = (dateString) => {
        const date = new Date(dateString);

        if (isNaN(date.getTime())) {
        console.error("Invalid date format:", dateString);
        return "Invalid date";
    }
        const formatted = new Intl.DateTimeFormat('en-IN', {
            timeZone: 'Asia/Kolkata',
            year: 'numeric',
            month: '2-digit',
            day: '2-digit',
            hour: '2-digit',
            minute: '2-digit',
            second: '2-digit',
            hour12: false
        });
        return formatted.format(date)
    }

    const handleDelete = (id) => {
        // Optionally make an API call to delete the feeder from the backend
        // fetch(`/api/feeder/${id}`, { method: 'DELETE' })

        // Update the state to remove the deleted feeder
        setDeletedItems([...deletedItems, id]);
    };

    const filteredData = details.filter(item => !deletedItems.includes(item.DT2));

    return (
        <div style={{ backgroundImage: `url(${image})`, backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: 'cover' }} className="flex flex-col bg-gray-300">
            <div className='container text-center m-3 space-x-2 space-y-2'>
                <h2>Feeder Details</h2>
                <table className='styled-table bg-red-100'>
                    <thead>
                        <tr>
                            <th>Station</th>
                            <th>Feeder Code</th>
                            <th>Feeder Name</th>
                            <th>From Date</th>
                            <th>To Date</th>
                            <th>Consumption Details</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredData.map((detail, index) => (
                            <tr key={index}>
                                <td>{detail.STATION}</td>
                                <td>{detail.FEEDER_CODE}</td>
                                <td>{detail.FEEDER_NAME}</td>
                                <td>{tableDate(detail.DT1)}</td>
                                <td>{tableDate(detail.DT2)}</td>
                                <td>
                                    <Link to={`/fetch-consumption/${detail.FEEDER_CODE}/${formatDate(detail.DT2)}/`}>Click here to view consumption</Link>
                                </td>
                                <td>
                                    <button className="delete-button" onClick={() => handleDelete(detail.DT2)}>Delete</button> {/* Delete button */}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default FetchDetails;
