//             )
//         }
//     />
// );


const App = () => {
    const [isLoggedIn, setIsLoggedIn] = useState(false);

    useEffect(() => {
        // Check login state from localStorage
        const loggedIn = localStorage.getItem('isLoggedIn');
        setIsLoggedIn(loggedIn === 'true');
    }, []);

    const handleLogin = () => {

        setIsLoggedIn(true);
        localStorage.setItem('isLoggedIn', 'true');
    };

    const handleLogout = () => {
        setIsLoggedIn(false);
        localStorage.removeItem('isLoggedIn');
    };

    return (
        <Router>
            <Routes>
                {isLoggedIn ? (
                    <>
                        <Route path="/" element={<FeederDetails onLogout={handleLogout} />} />
                        <Route path="/fetch-overall" element={<FetchDetails />} />
                        <Route path="/fetch-consumption/:feeder_code/:date" element={<ConsumptionDetails />} />
                    </>
                ) : (
                    <>
                        <Route path="/" element={<LoginPage onLogin={handleLogin} />} />
                    </>
                )}
            </Routes>
        </Router>
    );
}

export default App;