import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Select from 'react-select';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
// import makeAnimated from 'react-select/animated';
import { ClipLoader } from 'react-spinners';
import image from './background.jpg'
import { FixedSizeList as List } from 'react-window';
import { useNavigate } from 'react-router-dom';

const BASE_URL = 'http://10.50.44.16:8080/projects/';
// const animatedComponents = makeAnimated();

const customStyles = {
    control: (base) => ({
        ...base,
        minWidth: 400,
        whiteSpace: 'nowrap',
        marginBottom: 0 // Ensure no margin at the bottom
    }),
    menu: (base) => ({
        ...base,
        width: 400,
        whiteSpace: 'normal',
        wordWrap: 'break-word',
        overflow: 'hidden',
        marginTop: 0 // Ensure no margin at the top
    }),
    singleValue: (base) => ({
        ...base,
        overflow: 'hidden',
        wordWrap: 'break-word',
        whiteSpace: 'normal'
    }),
    multiValue: (base) => ({
        ...base,
        backgroundColor: 'lightgrey',
        overflow: 'hidden',
        wordWrap: 'break-word',
        whiteSpace: 'normal'
    }),
    multiValueLabel: (base) => ({
        ...base,
        color: 'black',
        overflow: 'hidden',
        wordWrap: 'break-word',
        whiteSpace: 'normal'
    }),
    multiValueRemove: (base) => ({
        ...base,
        color: 'red',
        ':hover': {
            backgroundColor: 'darkred',
            color: 'white',
        },
    }),
};

const customStylesInput = {
    control: (base) => ({
        ...base,
        width: 400,
        whiteSpace: 'nowrap',
        marginBottom: 0 // Ensure no margin at the bottom
    }),
    menu: (base) => ({
        ...base,
        width: 400,
        whiteSpace: 'normal',
        wordWrap: 'break-word',
        overflow: 'hidden',
        marginTop: 0 // Ensure no margin at the top
    }),
    singleValue: (base) => ({
        ...base,
        overflow: 'hidden',
        wordWrap: 'break-word',
        whiteSpace: 'normal'
    }),
    multiValue: (provided) => ({
        ...provided,
        backgroundColor: 'lightgrey',
        overflow: 'hidden',
        wordWrap: 'break-word',
        whiteSpace: 'normal'
    }),
    multiValueLabel: (provided) => ({
        ...provided,
        color: 'black',
        overflow: 'hidden',
        wordWrap: 'break-word',
        whiteSpace: 'normal'
    }),
    multiValueRemove: (provided) => ({
        ...provided,
        color: 'red',
        ':hover': {
            backgroundColor: 'darkred',
            color: 'white',
        },
    }),
};


const FeederDetails = ({ onLogout }) => {
    // const [selectedDefault, setSelectedDefault] = useState("");
    const [loading, setLoading] = useState(false);

    const [selectedStation, setSelectedStation] = useState('');
    const [selectedFeeder, setSelectedFeeder] = useState('');
    const [selectedDTR, setSelectedDTR] = useState([]);
    const [selectedHT, setSelectedHT] = useState([]);

    // const [fromDate, setFromDate] = useState('');
    // const [toDate, setToDate] = useState('');
    const [stationNames, setStationNames] = useState([]);
    const [feederDetails, setFeederDetails] = useState([]);
    const [htDetails, setHtDetails] = useState([]);

    const [allDTR, setAllDTR] = useState([]);
    const [fdrResult, setFdrResult] = useState([]);

    // const [error, setError] = useState(null);

    // const customStyles = {
    //     option: (provided) => ({
    //         ...provided,
    //         alignItems: 'center'
    //     }),
    //     control: (provided) => ({
    //         ...provided,
    //         minWidth: 240,
    //         minHeight: '50px'
    //     })
    // };

    const fetchFeederDetails = async (selectedValue) => {
        try {
            const response = await axios.get(BASE_URL + `api/feeder-names/${selectedValue}/`);
            setFeederDetails(response.data);
        } catch (error) {
            console.error('Error fetching feeder details:', error);
        }
    };

    // const fetchDtrDetails = async () => {
    //     try {
    //         const response = await axios.get(BASE_URL + `api/dtr/`);
    //         setDtrDetails(response.data);
    //     } catch (error) {
    //         console.error('Error fetching dtr details:', error);
    //     }
    // }

    const fetchHTDetails = async () => {
        try {
            const response = await axios.get(BASE_URL + `api/fetch-ht/`);
            setHtDetails(response.data);
        } catch (error) {
            console.error('Error fetching dtr details:', error);
        }
    }

    const fetchAllDTR = async () => {
        try {
            const response = await axios.get(BASE_URL + `api/dtr/`);
            setAllDTR(response.data);
            setLoading(false)
        } catch (error) {
            console.error('Error fetching dtr details:', error);
            setLoading(false);
        }
    }

    const fetchFeederAudit = async () => {
        try {
            const response = await axios.get(BASE_URL + `api/fetch-overall/`);
            setFdrResult(response.data);
        } catch (error) {
            console.error('Error fetching audit details:', error);
        }
    }

    const [products, setProducts] = useState(allDTR);
    const [htcons, setHtcons] = useState(htDetails);
    const [searchDT, setSearchDT] = useState("");
    const [searchHT, setSearchHT] = useState("");

    const [fromDate, setFromDate] = useState(new Date());
    const [toDate, setToDate] = useState(new Date());

    const fetchStationNames = async () => {
        try {
            const response = await axios.get(BASE_URL + 'api/station-names/');
            setStationNames(response.data);
        } catch (error) {
            console.error('Error fetching station names:', error);
        }
    };

    useEffect(() => {
        fetchStationNames();
        fetchAllDTR();
        fetchHTDetails();
        fetchFeederAudit();
    }, []);

    const handleStationChange = (event) => {
        const selectedStationId = event.target.value;
        setSelectedStation(selectedStationId);
        fetchFeederDetails(selectedStationId);
        // fetchDtrDetails(selectedStationId);
        // fetchHTDetails(selectedStationId);
    };

    const handleFeederChange = (event) => {
        const feeder = event.target.value
        setSelectedFeeder(feeder);
    };

    const handledtrchange = (event) => {
        setSelectedDTR(event || []);
    }

    const handleHtChange = (e) => {
        setSelectedHT(e || []);
    }

    // const [csrfToken, setCsrfToken] = useState('');

    // useEffect(() => {
    //     const fetchCsrfToken = async () => {
    //         try {
    //             const response = await axios.get('/api/get-csrf-token/');
    //             setCsrfToken(response.data.csrfToken);
    //         } catch (error) {
    //             console.error('Error fetching CSRF token:', error);
    //         }
    //     };

    //     fetchCsrfToken();
    // }, []);

    const handleDateSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);

        // Send a POST request to the backend
        // const feeder_encode = selectedFeeder.replaceAll('/', '');
        // const feeder_final = feeder_encode.replaceAll('//', '');

        const datatoSend = {
            station: selectedStation,
            feeder_name: selectedFeeder,
            dtrs: selectedDTR,
            hts: selectedHT,
            from_date: fromDate,
            to_date: toDate
        }

        // console.log(selectedDTR)
        // console.log(selectedHT)
        try {
            await axios.post(BASE_URL + `api/insert-overall-details/`, datatoSend)
            alert('Records inserted successfully');
            window.location.href = '/fetch-overall';
        } catch (error) {
            alert('Error inserting records.');
            // Handle error response (e.g., show an error message)
        } finally {
            setLoading(false); // Hide the loading spinner
        };

        setFromDate('');
        setToDate('');
        setSelectedDTR([]);
        setSelectedHT([])
    }

    const MenuList = ({ options, children, getValue }) => {
        const height = 400;
        const itemSize = 35;
        const [value] = getValue();
        const initialOffset = options.indexOf(value) * itemSize;

        return (
            <List
                height={height}
                itemCount={children.length}
                itemSize={itemSize}
                initialScrollOffset={initialOffset}
            >
                {({ index, style }) => <div style={style}>{children[index]}</div>}
            </List>
        );
    };

    const navigate = useNavigate();

    const handleLogout = () => {
        // Clear the saved login info from localStorage
        onLogout();
        // Redirect to the login page
        navigate('/');
    };

    return (
        <div>
            {loading ? (
                <div className="loading-container">
                    <ClipLoader size={50} color={"#123abc"} loading={loading} />
                    <p>Loading, please wait...</p>
                </div>
            ) : (
                <div style={{ backgroundImage: `url(${image})`, backgroundRepeat: 'no-repeat', height: '100vh', backgroundSize: 'cover' }} className="flex flex-col bg-gray-300">
                    <div className="w-auto bg-amber-200">
                        <h1 style={{ fontFamily: 'Century Gothlic' }} className="text-center text-4xl">DTR FEEDER INFORMATION</h1>
                    </div>
                    <div className='container m-4 space-x-2 space-y-2'>
                        <div style={{ fontSize: '16px' }} className="flex space-x-2">
                            <label className="font-bold">Station:</label>
                            <select style={{ width: '120px', height: '30px' }} className="bg-white" onChange={handleStationChange}>
                                <option value={selectedStation}>Select All</option>
                                <input
                                    type="checkbox"
                                    id="selectAll"
                                />
                                {stationNames.map((station, index) => (
                                    <option key={index}>
                                        {station.STATION}
                                    </option>

                                ))}
                            </select>
                            <label className="font-bold">feeder:</label>
                            <select style={{ width: '120px', height: '30px' }} className="bg-white" onChange={handleFeederChange}>
                                <option value={selectedFeeder}>Select All</option>
                                <input
                                    type="checkbox"
                                    id="selectAll"
                                />
                                {feederDetails.map((feeder, index) => (
                                    <option key={index}>
                                        {feeder.FEEDER_NAME + ' (' + feeder.FEEDER_CODE + ')'}
                                    </option>
                                ))}
                            </select>
                            <label className="font-bold">DTR:</label>
                            {searchDT ? <Select
                                components={{ MenuList }}
                                isMulti
                                options={products}
                                onChange={handledtrchange}
                                getOptionLabel={(option) => option.SNAME + ' (' + option.SCODE + ')'}
                                getOptionValue={(option) => option.SNAME + ' (' + option.SCODE + ')'}
                                styles={customStyles}
                                classNamePrefix="select"
                                closeMenuOnSelect={false}
                            /> : <Select
                                components={{ MenuList }}
                                isMulti
                                options={allDTR}
                                onChange={handledtrchange}
                                getOptionLabel={(option) => option.SNAME + ' (' + option.SCODE + ')'}
                                getOptionValue={(option) => option.SNAME + ' (' + option.SCODE + ')'}
                                styles={customStyles}
                                classNamePrefix="select"
                                closeMenuOnSelect={false}
                            />}
                            <label className="font-bold">HTCons:</label>
                            {searchHT ? <Select
                                components={{ MenuList }}
                                isMulti
                                options={htcons}
                                onChange={handleHtChange}
                                getOptionLabel={(option) => option.EN1 + ' (0' + option.CONS_NUM + ')'}
                                getOptionValue={(option) => option.EN1 + ' (0' + option.CONS_NUM + ')'}
                                styles={customStylesInput}
                                classNamePrefix="select"
                                closeMenuOnSelect={false}
                            /> : <Select
                                components={{ MenuList }}
                                isMulti
                                options={htDetails}
                                onChange={handleHtChange}
                                getOptionLabel={(option) => option.EN1 + ' (0' + option.CONS_NUM + ')'}
                                getOptionValue={(option) => option.EN1 + ' (0' + option.CONS_NUM + ')'}
                                styles={customStylesInput}
                                classNamePrefix="select"
                                closeMenuOnSelect={false}
                            />}
                        </div>
                    </div >
                    <div className="text-center space-x-2">
                        <label className='font-bold'>From Date: </label>
                        <input type='date'
                            value={fromDate}
                            onChange={(date) => setFromDate(date.target.value)}
                       
                        />
                        <label className='font-bold'>To Date: </label>
                        <input type='date'
                            value={toDate}
                            onChange={(date) => setToDate(date.target.value)}
                       
                        />
                        <br /><br />
                        <button style={{ border: '1px solid black', borderRadius: '14px', fontFamily: 'century-gothlic' }}
                            className="bg-blue-500 btn btn-primary hover:bg-white"
                            onClick={handleDateSubmit}
                        >Submit
                        </button>
                        <button style={{ border: '1px solid black', borderRadius: '14px', fontFamily: 'century-gothlic' }}
                            className="bg-red-500 btn btn-primary hover:bg-white"
                            onClick={handleLogout}
                        >Log Out
                        </button>
                    </div>
                </div>
            )}
        </div>
    );
};

export default FeederDetails;
