import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import image from './background.jpg';
import * as XLSX from 'xlsx';

const BASE_URL = 'http://10.50.44.16:8080/projects/';


const ConsumptionDetails = () => {
    const { feeder_code, date } = useParams();
    const [details, setDetails] = useState(null);
    const [loading, setLoading] = useState(true);
    const [autoRefresh, setAutoRefresh] = useState(true);
    const [error, setError] = useState(null);

    const fetchDetails = async () => {

        setLoading(true);
        try {
            const response = await axios.get(BASE_URL + `api/fetch-consumption/${feeder_code}/${date}/`);
            setDetails(response.data);
            setLoading(false);
            setAutoRefresh(false);
        } catch (error) {
            setError('Error fetching consumption details');
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchDetails();

        const intervalId = setInterval(() => {
            if (autoRefresh) {
                fetchDetails();
            }
        }, 30000);

        // Clean up interval on component unmount
        return () => clearInterval(intervalId);
    }, [autoRefresh]);

    if (loading) return <p>Loading...</p>;
    if (error) return <p>{error}</p>;

    const tableDate = (date) => {
        const value = date.slice(0, 10);
        return `${value}`
    }

    const downloadExcel = () => {
        const formattedData = details.map((item) => ({
            'Station': item.STATION,
            'Feeder Name': item.FEEDER_NAME,
            'Date': tableDate(item.DT),
            'Feeder Sigma KWH': item.FEEDER_SIGMA_KWH ? item.FEEDER_SIGMA_KWH : 0,
            'DTR Sigma KWH': item.DTR_SIGMA_KWH ? item.DTR_SIGMA_KWH : 0,
            'DTR Sigma KWH Count': item.DTR_SIGMA_KWH_COUNT ? item.DTR_SIGMA_KWH_COUNT : 0,
            'HTC Sigma KWH': item.HTC_SIGMA_KWH ? item.HTC_SIGMA_KWH : 0,
            'HTC Sigma KWH Count': item.HTC_SIGMA_KWH_COUNT ? item.HTC_SIGMA_KWH_COUNT : 0,
            'Loss in Consumption': ((item.FEEDER_SIGMA_KWH - (item.DTR_SIGMA_KWH + item.HTC_SIGMA_KWH)) / (item.FEEDER_SIGMA_KWH) * 100).toFixed(2)
        }));

        const worksheet = XLSX.utils.json_to_sheet(formattedData);
        const workbook = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(workbook, worksheet, 'Consumption Details');

        // Format the file name
        const fileName = 'consumption_details.xlsx';

        // Write the Excel file and trigger the download
        XLSX.writeFile(workbook, fileName);
    };

    return (
        <div style={{ backgroundImage: `url(${image})`, backgroundRepeat: 'no-repeat', backgroundPosition: 'center', backgroundSize: 'cover' }} className="flex flex-col bg-gray-300">
            <div className='container text-center m-3 space-x-2 space-y-2'>
                <h2>Feeder Consumption </h2>
                {loading ? (
                    <div className="spinner"></div>
                ) : (
                    <div>
                        <button style={{ marginLeft: '80%', color: 'blue', textDecoration: 'underline' }} onClick={downloadExcel}>Download Excel</button>
                        <table className='styled-table bg-red-100'>
                            <thead>
                                <tr>
                                    <th>Station</th>
                                    <th>Feeder Code</th>
                                    <th>Feeder Name</th>
                                    <th>From Date</th>
                                    <th>Feeder Sigma kWh</th>
                                    <th>Feeder Count for Sigma kWh</th>
                                    <th>DTR Sigma kWh</th>
                                    <th>DTR Count for Sigma kWh</th>
                                    <th>HTC Sigma kWh</th>
                                    <th>HTC Count for Sigma kWh</th>
                                    <th>Loss Percentage</th>
                                </tr>
                            </thead>
                            <tbody>
                                {details.map((detail) => (
                                    <tr key={detail.id}>
                                        <td>{detail.STATION}</td>
                                        <td>{detail.FEEDER_CODE}</td>
                                        <td>{detail.FEEDER_NAME}</td>
                                        <td>{tableDate(detail.DT)}</td>
                                        <td>{detail.FEEDER_SIGMA_KWH}</td>
                                        <td>{detail.FEEDER_SIGMA_KWH_COUNT}</td>
                                        <td>{detail.DTR_SIGMA_KWH}</td>
                                        <td>{detail.DTR_SIGMA_KWH_COUNT}</td>
                                        <td>{detail.HTC_SIGMA_KWH}</td>
                                        <td>{detail.HTC_SIGMA_KWH_COUNT}</td>
                                        <td>{((detail.FEEDER_SIGMA_KWH - (detail.DTR_SIGMA_KWH + detail.HTC_SIGMA_KWH)) / (detail.FEEDER_SIGMA_KWH) * 100).toFixed(2)}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                )}
            </div>
        </div >
    );
};

export default ConsumptionDetails;
