/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react';
import image from './dtr.jpg';
import { useNavigate } from 'react-router-dom';
// import LoadingSpinner from './LoadingSpinner';

const LoginPage = ({ onLogin }) => {
    // const [loading, setLoading] = useState(false);
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const navigate = useNavigate();

    const handleLogin = () => {
        // Here you would typically perform authentication logic
        // For simplicity, let's assume the login is successful if both username and password are 'admin'
        if (username === '940027' && password === 'P@ssw0rd1') {
            onLogin();
            navigate('/');
        } else if (username === '977355' && password === 'P@ssw0rd2') {
            onLogin();
            navigate('/');
        } else {
            alert('Invalid username or password');
        }
    };

    return (
        <div style={{ backgroundImage: `url(${image})`, backgroundSize: 'cover' }} className="min-h-screen flex flex-col items-center justify-center bg-sky-300">
            <div className="bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4">
                <h1 className="text-2xl font-bold text-center mb-4">FEEDER ENERGY AUDIT</h1>
                <form onSubmit={handleLogin}>
                    <div className="mb-4">
                        <label htmlFor="username" className="block text-gray-700 font-bold mb-2">
                            Username
                        </label>
                        <input
                            type="text"
                            id="username"
                            name="username"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                        />
                    </div>
                    <div className="mb-6">
                        <label htmlFor="password" className="block text-gray-700 font-bold mb-2">
                            Password
                        </label>
                        <input
                            type="password"
                            id="password"
                            name="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                        />
                    </div>
                    <div className="flex items-center justify-between">
                        <button
                            type="submit"
                            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
                        >
                            Sign In
                        </button>
                        <a href="#"
                            className="inline-block align-baseline font-bold text-sm text-blue-500 hover:text-blue-700">
                            Forgot Password?
                        </a>
                    </div>
                </form>
            </div>

        </div>
    );
};

export default LoginPage;
